function main() {
	document.body.classList.add('started');

	const width = document.querySelector('html')?.offsetWidth ?? 0;
	const height = document.querySelector('html')?.offsetHeight ?? 0;
	const row = Math.floor(height / 128);
	const column = Math.floor(width / 128);

	const copyEl = document.getElementById('copy') as HTMLVideoElement;

	if (!copyEl) throw new Error();

	copyEl.removeAttribute('style');
	copyEl.remove();

	const catsEl = document.getElementById('cats');

	catsEl?.setAttribute('style', `grid-template-columns: repeat(${column}, 128px);`);

	for (let index = 0; index < column * row; index++) {
		const el = copyEl.cloneNode() as HTMLVideoElement;
		catsEl?.appendChild(el);
	}

	window.addEventListener('click', () => {
		document.getElementById('start')?.remove();
		const videos = document.querySelectorAll('video');
		let index = -1;

		setInterval(() => videos[++index]?.play(), 300);
	}, {
		once: true
	});
}

window.addEventListener('load', main);